.container {
  width: 100%;
  max-width: 672px;
  margin: 0 auto;
  padding: 16px;
  padding-top: 22px;
  box-sizing: border-box;
}

.cards {
  display: flex;
  flex-direction: row;
  gap: 10px;
  flex-wrap: wrap;
}

.modalContainer {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #004980c0;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: end;
  margin-bottom: 35px;
}

.timer {
  display: flex;
  align-items: flex-end;

  color: #fff;
  font-family: StratosSkyeng;
  font-size: 64px;
  font-style: normal;
  font-weight: 400;
  line-height: 72px;
}

.previewText {
  font-size: 30px;
  line-height: 30px;
  margin-top: 34px;
  margin-bottom: 10px;
}
.previewDescription {
  font-size: 18px;
  line-height: 18px;
}

.timerValue {
  text-align: right;
}

.timerDescription {
  color: #fff;
  font-variant-numeric: lining-nums proportional-nums;
  font-family: StratosSkyeng;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 32px;

  margin-bottom: -12px;
}

.tries {
  color: greenyellow;
  display: block;
}

.exit {
  cursor: pointer;
  position: relative;
  width: 32px;
  height: 32px;
  background-image: url('/public/exit.svg');
  background-repeat: no-repeat;
  bottom: 4px;
}
@media (max-width: 720px) {
  .container {
    padding: 10px;
    width: 100%;
  }

  .header {
    flex-direction: row; /* Оставляем горизонтальное расположение */
    align-items: center;
    margin-bottom: 20px;
    flex-wrap: wrap;
    justify-content: center;
    gap: 15px;
  }

  .timer {
    font-size: 36px;
    line-height: 40px;
  }

  .timerDescription {
    font-size: 14px;
    margin-bottom: -8px;
  }

  .cards {
    margin-top: 50%;
    justify-content: center;
    gap: 8px; /* Уменьшаем промежутки между картами */
  }

  /* Дополнительно для очень маленьких экранов */
  @media (max-width: 400px) {
    :root {
      --card-width: min(50px, 18vw);
      --card-height: min(75px, 27vw);
    }
    
    .timer {
      font-size: 32px;
    }
  }
}